import { useState } from "react";

export function AddService() {
  const [fin, setFin] = useState('');
  const [km, setKm] = useState('');
  const [service, setService] = useState('');

  const handleAddKm = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    console.log('FIN:', fin);
    console.log('KM:', km);

    const formData = new FormData(e.currentTarget);
    const formJson = Object.fromEntries(formData.entries());

    try {
      const response = await fetch('https://it200287.cloud.htl-leonding.ac.at/api/maintenance/addKm', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formJson),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const result = await response.json();
      console.log('Server Response:', result);

      // Optional: Reset inputs
      setFin('');
      setKm('');

      // Optional: Erfolgsmeldung anzeigen
      alert('Kilometer erfolgreich hinzugefügt!');
    } catch (error) {
      console.error('Fehler beim Hinzufügen der Kilometer:', error);
      alert('Fehler beim Speichern! Bitte erneut versuchen.');
    }
  };

  return (
    <div
      style={{
        maxWidth: 400,
        margin: '0 auto',
        padding: 30,
        border: '1px solid #f97316',
        borderRadius: 12,
        backgroundColor: '#1e1e1e',
        color: '#f97316',
        fontFamily: 'Arial, sans-serif',
      }}
    >
      <h2 style={{ textAlign: 'center', marginBottom: 20 }}>KTM Bike - Kilometer hinzufügen</h2>

      <form onSubmit={handleAddKm}>
        <div style={{ marginBottom: 15 }}>
          <label htmlFor="finInput" style={{ display: 'block', marginBottom: 5 }}>
            FIN:
          </label>
          <input
            id="finInput"
            name="fin"
            placeholder="FIN eingeben"
            value={fin}
            onChange={(e) => setFin(e.target.value)}
            style={{
              width: '100%',
              padding: 10,
              borderRadius: 6,
              border: '1px solid #ccc',
            }}
            required
          />
        </div>

        <div style={{ marginBottom: 20 }}>
          <label htmlFor="kmInput" style={{ display: 'block', marginBottom: 5 }}>
            Kilometer hinzufügen:
          </label>
          <input
            id="kmInput"
            name="km"
            type="number"
            placeholder="KM"
            value={km}
            onChange={(e) => setKm(e.target.value)}
            style={{
              width: '100%',
              padding: 10,
              borderRadius: 6,
              border: '1px solid #ccc',
            }}
            required
          />
        </div>

        <button
          type="submit"
          style={{
            width: '100%',
            padding: 12,
            backgroundColor: '#f97316',
            color: '#fff',
            fontWeight: 'bold',
            border: 'none',
            borderRadius: 6,
            cursor: 'pointer',
            transition: 'background-color 0.3s',
          }}
          onMouseOver={(e) => (e.currentTarget.style.backgroundColor = '#ea580c')}
          onMouseOut={(e) => (e.currentTarget.style.backgroundColor = '#f97316')}
        >
          Speichern
        </button>
      </form>
    </div>
  );
}
